AAA;EACA,kBAAA;EACA,eAAA;EACA,cAAA;EACA,sBAAA;EACA,oBAAA;EACA,qBAAA;EACA,mBAAA;EACA,iBAAA;EACA,cAAA;EACA,mCAAA;AI2hLF;AJxhLA;EAAwB,eAAA;AI4hLxB;AJ3hLA;EAAwB,eAAA;AI+hLxB;AJ9hLA;EAAwB,eAAA;AIkiLxB;AJjiLA;EAAwB,eAAA;AIqiLxB;AJniLA;EACE,SAAA;EACA,aAAA;EACA,+CAAA;EACA,eAAA;EACA,cAAA;AIsiLF;AJniLA;EACE,YAAA;AIsiLF;AJniLA;EACE,aAAA;EACA,SAAA;EACA,WAAA;EACA,gBAAA;EACA,8EAAA;AIsiLF;AJ/hLA;EACE,aAAA;AIkiLF;AJ/hLA;EACE,aAAA;EACA,mBAAA;EACA,mBAAA;EACA,YAAA;AIkiLF;AJ/hLA;EACE,YAAA;AIkiLF;AJ/hLA;EACE,eAAA;EACA,kBAAA;EACA,cAAA;AIkiLF;AJ/hLA;EACE,eAAA;EACA,iBAAA;EACA,cAAA;EACA,cAAA;AIkiLF;AJ/hLA;EACE,YAAA;EACA,cAAA;EACA,gBAAA;AIkiLF;AJ/hLA;EACE,mBAAA;AIkiLF;AJ/hLA;EACE,SAAA;AIkiLF;AJ/hLA;EACE,oBAAA;EACA,mBAAA;AIkiLF;AJ/hLA;EACE,UAAA;AIkiLF","file":"styles.scss","sourcesContent":["/* You can add global styles to this file, and also import other style files */\n@import './assets/scss/defined-break-points';\n@import './assets/scss/themes';\n@import './assets/scss/font';\n@import '~ngx-toastr/toastr';\n// @import '~tippy.js/dist/tippy.css'; // optional for styling\n// @import '~jsoneditor/dist/jsoneditor.css';\n\n* {\n  box-sizing: border-box;\n}\n\n.material-icons {\n  font-family: 'Material Icons';\n  font-weight: normal;\n  font-style: normal;\n  font-size: 24px;\n  line-height: 1;\n  letter-spacing: normal;\n  text-transform: none;\n  display: inline-block;\n  white-space: nowrap;\n  word-wrap: normal;\n  direction: ltr;\n  -webkit-font-smoothing: antialiased;\n}\n\n.material-icons.md-18 { font-size: 18px; }\n.material-icons.md-24 { font-size: 24px; }\n.material-icons.md-36 { font-size: 36px; }\n.material-icons.md-48 { font-size: 48px; }\n\nbody {\n  margin: 0;\n  height: 100vh;\n  font-family: 'Open Sans', sans-serif !important;\n  font-size: 13px;\n  color: #292a1d;\n}\n\n.app-root {\n  height: 100%;\n}\n\n.main-header {\n  padding: 24px;\n  top: 54px;\n  width: 100%;\n  background: #fff;\n  box-shadow: 0 1px 3px 0 rgba(77,77,77,0.2), 0 1px 1px 0 rgba(0,0,0,0.05);\n}\n\n.main-header.tab-bar {\n  // height: 96px;\n}\n\n.main-header.secondary.tab-bar {\n  height: 128px;\n}\n\n.header-primary {\n  display: flex;\n  align-items: center;\n  padding-bottom: 8px;\n  height: 32px;\n}\n\n.header-primary > .flex-spacer {\n  flex-grow: 1;\n}\n\n.header-primary > .header-title {\n  font-size: 20px;\n  padding-right: 8px;\n  color: #3c3c3c;\n}\n\n.header-primary > .header-sub-title {\n  font-size: 13px;\n  line-height: 16px;\n  padding: 8px 0;\n  color: #979797;\n}\n\n.header-secondary {\n  height: 32px;\n  padding: 6px 0;\n  overflow-y: auto;\n}\n\n.main-content {\n  margin: 16px 24px 0;\n}\n\n.main-content.tab-content {\n  margin: 0;\n}\n\np,h1,h2,h3,h4,h5,h6 {\n  margin-inline: unset;\n  margin-block: unset;\n}\n\ninput, button {\n  outline: 0;\n}\n","// Breakpoints\n$handset: 360px;\n$large-handset: 480px;\n$tablet: 640px; /* app drawer breakpoint */\n$large-tablet: 840px;\n$desktop: 960px;\n$large-desktop: 1280px;\n$x-large-desktop: 1440px;\n$xx-large-desktop: 1920px;\n\n// generate min-width breakpoint\n@mixin breakpoint($point) {\n  @media (min-width: $point) { @content ; }\n}\n// generate max-width breakpoint (use sparingly)\n@mixin breakpoint-max($point) {\n  @media (max-width: $point) { @content ; }\n}\n// generate min-height breakpoint\n@mixin height-breakpoint($point) {\n  @media (min-height: $point) { @content ; }\n}\n// generate max-height breakpoint (use sparingly)\n@mixin height-breakpoint-max($point) {\n  @media (min-height: $point) { @content ; }\n}\n","// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    // Note: it's important for this one to start off `absolute`,\n    // in order for us to be able to measure it correctly.\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n\n    // For connected-position overlays, we set `display: flex` in\n    // order to force `max-width` and `max-height` to take effect.\n    display: flex;\n    max-width: 100%;\n    max-height: 100%;\n  }\n\n  .cdk-overlay-backdrop {\n    // TODO(jelbourn): reuse sidenav fullscreen mixin.\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 1;\n\n      // In high contrast mode the rgba background will become solid so we need to fall back\n      // to making it opaque using `opacity`. Note that we can't use the `cdk-high-contrast`\n      // mixin, because we can't normalize the import path to the _a11y.scss both for the\n      // source and when this file is distributed. See #10908.\n      @media screen and (-ms-high-contrast: active) {\n        opacity: 0.6;\n      }\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from\n    // capturing the user's mouse scroll events. Since we also can't use something like\n    // `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at\n    // all and using `opacity` to make the element transparent.\n    &, &.cdk-overlay-backdrop-showing {\n      opacity: 0;\n    }\n  }\n\n  // Overlay parent element used with the connected position strategy. Used to constrain the\n  // overlay element's size to fit within the viewport.\n  .cdk-overlay-connected-position-bounding-box {\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n\n    // We use `display: flex` on this element exclusively for centering connected overlays.\n    // When *not* centering, a top/left/bottom/right will be set which overrides the normal\n    // flex layout.\n    display: flex;\n\n    // We use the `column` direction here to avoid some flexbox issues in Edge\n    // when using the \"grow after open\" options.\n    flex-direction: column;\n\n    // Add some dimensions so the element has an `innerText` which s